@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;600&display=swap);
*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
a{
  text-decoration: none;
  color: lightgray;
}
body {
  margin: 0;
  font-family: 'Rubik', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  color: white;
  max-width:1100px ;
  margin:0 auto;
}

.App{padding:30px;text-align:left}.won-card{background-color:#4da375}.lost-card{background-color:#a34d5d}.more-link{align-self:center;justify-self:center;size:1.5rem}

.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:20px}.MatchDetailCard .match-date,.MatchDetailCard .match-venue,.MatchDetailCard .match-result{margin-top:10px}.MatchDetailCard .additional-detail{text-align:right}.MatchDetailCard .additional-detail>p{margin-bottom:20px}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-row-gap:10px;grid-column-gap:10px}.TeamPage .team-name-section{grid-column:span 3;justify-self:start;align-self:end}.TeamPage .team-name{font-size:4rem;margin-bottom:20px}.TeamPage .wins-loss-section{justify-self:center;padding:40px;text-align:center}.TeamPage .match-detail-section{grid-column:span 4}

.YearSelector{text-align:right;margin-bottom:10px;margin-top:10px}

.MatchPage{display:grid;grid-template-columns:1fr 6fr;grid-column-gap:20px}.MatchPage .year-selector{padding:10px;justify-self:end;padding:20px}.MatchPage .page-heading{margin:20px auto}

.TeamTile{height:170px;text-align:center;border:1px solid white;padding:20px}

.HomePage .app-name{size:3rem;margin:30px}.HomePage .team-grid{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}

